import React from "react";
import {
  Link, Redirect
} from 'react-router-dom';
import Loader from 'react-loader-spinner'

class SexGame extends React.Component {
  constructor () {
    super();
    this.state = {
      display: "inherit",
      display2:"none",
      display3:"inherit",
      opacity:0,
      opacity2:0
    }
    this.changeDisplay= this.changeDisplay.bind(this);
    this.changeOpacity= this.changeOpacity.bind(this);
  }
  
  componentDidMount () {

  }
  changeOpacity(){
  	this.setState({
  		opacity:1,
  		display3:"none",
  	});
  	
  }
  
  changeDisplay(){
  	this.setState({
  		opacity:0
  	});
  	setTimeout(() => {
      this.setState({
      	display2:"inherit",
  		display:"none"
  	});
    
  }, 300)
  	setTimeout(() => {
      this.setState({
  		display2:"inherit",
  		opacity2:1
  	});
    
  }, 650)
  	
  }
  render () {

    return (
    	<div className="container" style={{width:window.innerWidth, height:window.innerHeight}}>
    	<div className="button_dot" style={{display:this.state.display3}}>&nbsp;
    	</div>
      <div className="sexGamePopup_container" style={{display:this.state.display,opacity:this.state.opacity}}>
      <div className="sexGamePopup" onClick={this.changeOpacity}>
      Do you want to have sex with Nahee.app?
      </div>
      <div className="sexGamePopup_yes" onClick={this.changeDisplay}>
      Yes
      </div>
      <div className="sexGamePopup_yes">
      No
      </div>
      
      </div>
 
      <div className="sexGamePopup_container2" style={{height:"200px",display:this.state.display2,opacity:this.state.opacity2}}>
      <div className="sexGamePopup2">
      Checking<br/>compatibility
      </div>
      <div className="sexGamePopup_loading">
      <Loader 
         type="MutatingDot"
         color="#000000"
         height="120"	
         width="120"
      />  

      </div>
      
      
      
      </div>
      </div>
    );
  }
}

export default SexGame;